@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;1,400&family=Roboto:wght@100;300;400;700&display=swap);
:root {
  --brand-white: #ffffff;
  --brand-black: #000000;
  --brand-grey: rgb(235, 235, 235);
  --brand-font-primary: "Montserrat", Helvetica, Arial, sans-serif;
  --brand-font-secondary: "Roboto", Helvetica, Arial, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

h1,
h2,
h3 {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-family: var(--brand-font-primary);
}

a {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-family: var(--brand-font-secondary);
}

b, strong {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-family: var(--brand-font-secondary);
  font-weight: 700;
}

a:link,
a:visited {
  text-decoration: none;
}

.container {
  /* padding: 0 50px 0 50px; */
  width: 100%;
  height: 100%;
  background-color: rgb(235, 235, 235);
  background-color: var(--brand-grey);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.container h2 {
  font-size: 2rem;
}
.nav-container {
  background-color: #000000;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  top: 10;
  width: 100%;
  z-index: 1;
}
.nav-logo > img {
  padding: 3vh;
  margin-left: 5vh;
  height: 100%;
}
.footer-container {
  background-color: #000000;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  color: white;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media screen and (max-width: 365px) {
  h3 {
    font-size: 1rem;
  }
}
.hero-container {
  width: 100%;
  padding-top: 80px; /* remove if changing on scroll behaviour*/
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
}

.hero-video {
  object-fit: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.hero-content {
  padding: 0 100px 0 50px;
}

.hero-content > h1,
h2 {
  color: #ffffff;
}

.hero-content > h1 {
  font-size: 10vw;
  margin-bottom: 10px;
  line-height: 1em;
}

.hero-content > h2 {
  font-size: 5vw;
  font-weight: 100;
  margin-bottom: 40px;
}

@media screen and (min-width: 769px) {
  .hero-content > h2 {
    font-size: 3vw;
  }
}

a.button {
  width: 250px;
  padding: 2rem;
  background-color: transparent;
  border: 1px #ffffff solid;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 2px;
  color: #ffffff;
  font-size: 1.6rem;
}

a.button:hover {
  background-color: #ffffff;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  color: #000000;
}

.card-item-wrapper {
  width: 300px;
  padding: 1.2rem;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.card-item-wrapper img {
  height: auto;
  max-width: 100%;
  width: 100%;
}

.card-item-content {
  padding: 1rem;
  background-color: #ffffff;
}

.card-item-content h3 {
  font-size: 1.7rem;
  margin-bottom: 2rem;
}

.card-item-content a {
  font-size: 1.6rem;
  margin-bottom: 2rem;
  display: block;

}

.card-item-content a:visited {
  color: #000000;
}

.card-item-content a:hover {
  text-decoration: underline;
}

.card-button {
  padding: 1rem 2rem;
  background: #000000;
  color: #fff;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}

.toggle-container {
  display: -webkit-flex;
  display: flex;
  color: white;
  -webkit-justify-content: center;
          justify-content: center;
}

 .toggle-container input {
  display: none;
} 

.toggle-container label {
  font-size: 1.6rem;
  padding: 2rem;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  border: solid 1px white;
  width: 150px;
}

.toggle-container label:hover {
  cursor: pointer;
}

.toggle-container input:checked {
  background-color: white;
  color: #000;
}
.feature-container {
  text-align: center;
  width: 100%;
  display: inline-block;
  background-color: black;
  padding: 10rem 0 10rem 0;
}

.feature-container h2 {
  font-size: 2rem;
  margin-bottom: 2.5rem;
}

.feature-container h3 {
  font-size: 1.6rem;
  color: white;
  font-weight: 100;
}
.modal-background {
  width: 100%;
  height: 100vh;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
}

.modal-wrapper {
  width: 80vw;
  height: 100vh;
  background: rgb(235, 235, 235);
  color: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  padding: 3rem;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  line-height: 2;
}

.modal-text > ol,
li,
b,
h3 {
  font-size: 1rem;
}

.modal-text > p {
  margin-top: 10px;
  font-size: 1rem;
  font-weight: 700;
}

.modal-wrapper button {
  padding: 1rem 2rem;
  background: #000000;
  color: #fff;
  border: none;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  position: absolute;
  top: 20px;
  right: 20px;
}

button:focus {
  border: none;
  outline: none;
}

button:hover {
  opacity: 0.5;
}

.modal-wrapper img {
  color: #fff;
  max-width: 30vh;
  margin-bottom: 2rem;
  margin-top: 20px;
}

@media screen and (min-width: 769px) {
  .modal-text > ol,
  li,
  b,
  h3, p {
    font-size: 1.3rem;
  }
  .modal-wrapper img {
    max-width: 50vh;
  }

  button {
    font-size: 1.3rem;
  }
}

